* {
    box-sizing: border-box;
    margin: 0;
    padding: 0%;
}

html {
    background-color: white;
}


nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: black;
    height: 50px;

    a {
        text-align: center;
        display: block;
        align-items: center;
        height: 100%;
        width: 100%;
        color: white;
        text-decoration: none;

        &:nth-child(1) {
            background-color: white;
        }
        
    }


}




.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: url("../img/aneta-webbie-background.jpg");
    color: white;
    margin-top: 30px;
    padding: 300px;

    h1 {
        font-size: 30px;
    }
}